import React,{Component} from 'react'
class Footer extends Component {
    render() {
        return <div style={{backgroundColor:"#000",fontSize:"12px",color:"#fff",padding:"25px 0",textAlign:"center"}}>
            <span data-v-ba5e338c="">{localStorage['language'] == 'en'?'customer service':'客服邮箱'}:<a  href="mailto:create_help@baidu.com" style={{color:'#fff'}}>create_help@baidu.com</a></span>
            {/*<div style={{marginTop:"8px"}}>©2019 Baidu<a href="https://www.baidu.com/duty/">使用百度前必读</a></div>*/}
            <div style={{marginTop:"8px"}}>Copyright ©2019 Baidu, Inc. All Rights Reserved.</div>
        </div>
    }
}
export default Footer


