"use strict";
import React,{Component, PropTypes} from 'react'
import './Pay.scss'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import en from '../../../locale/en';
import {browserHistory} from 'react-router'
import zh from '../../../locale/zh';
import Constant from '../../../components/lib/Constant.js'
import Params from '../../../components/lib/QueryParams.js'
import Header from '../../../components/Header/Header'
import Footer from '../../../components/Footer/Footer'
import $ from 'n-zepto'
import {FormattedMessage} from '../../../components/lib/BaseComponent'

let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class Phone extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {

    }
    render() {
        return <div style={{backgroundColor:"#fff"}}>
            <Header/>
            <div style={{margin:"20px auto",textAlign:"center",minHeight:"680px",fontSize:"20px"}}>
                <FormattedMessage id="pay_success"/>
            </div>
            <Footer/>
        </div>
    }
}
class PC extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        $.getScript = function (url, success, error) {
            let script = document.createElement("script"),
                $script = $(script);
            script.src = url;

            $("head").append(script);
            $script.bind("load", success);
            $script.bind("error", error);
        };
        // todo bug
        this.init =()=>{
            $.get((Params('type') === '1'?'/aiweb/pay/payStudent.do':'/aiweb/pay/payPc.do'), (data) => {
                if (data.error_no === '0') {
                    new lbscashier({
                        onmessage: function (response) {
                            // console.log(response.data);
                            if(response.data === 'success' && response.type === 'pay') {
                                browserHistory.push(Constant.router_preffix + '/pay_success');
                                // console.log('I dojsdiofjoisd');
                            }
                        },
                        containerId: 'lbscashier',
                        data: data.data,
                        height: 800,
                        disabledChannel: [],
                        hideDisabledChannel: 0,
                        asycSubmit: 0,
                        payCheckUrl: '',
                        samePage: 0
                    });
                }
                else {
                    //没有要支付的票 etc...
                }
            });
        };
        $.getScript('https://zhifu.baidu.com/static/js/pc/iframe/lbscashier-iframe.js?t='+new Date().getTime(), ()=>{
            this.init();
        });

    }
    render() {
        return <div style={{backgroundColor:"#fff"}}>
            <Header/>
            <div id="lbscashier" style={{minHeight:"800px",padding:'100px 0 0 50px'}}/>
            <Footer/>
        </div>
    }
}
export class Pay extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {

    }
    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}
export default Pay;


